/* Week5.css */
body{
    background-color: #9577ff;
}

.cont_1,
.cont_2 {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .card-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
  }
  
  .card {
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: hidden;
    background-color: #e66161;
    box-shadow: 0 4px 8px rgba(255, 5, 5, 0.1);
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
    
  }
  
  .card:hover {
    box-shadow: 10px 6px 10px rgba(255, 0, 0, 0.1);
    transform: translateY(-15px);
  }
  
  .card-body {
    padding: 15px;
  }
  
  .card-title {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .card-text {
    font-size: 14px;
    margin-bottom: 5px;
  }
  .cont_1 {
    background-color: #00ffae;
    max-width: 50%;
    border: 10px solid rgb(255, 4, 4); /* Border thickness, style, and color */
    border-radius: 25px;
}

.cont_2{
    background-color: #d400ff;
    max-width: 50%;
    border: 10px solid rgb(238, 255, 0); /* Border thickness, style, and color */
    border-radius: 25px;
}

.gap{
    height: 20px;
}

